<template>
	<div id="timeOfSecKill">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #000000 !important;"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title" style="max-width: 80%;text-align: left">
          <img src="static/img/img_xsms_sj.png" style="height: 17px;vertical-align: middle;">
        </div>
      </div>
    </div>
    <div class="body_box">
      <!--<div v-show="false" class="background_color_ffffff" style="height: 30px;line-height: 30px;text-align: center">-->
        <!--<img src="static/img/icon_gx_xsms.png" style="width: 15px;vertical-align: middle;">-->
        <!--<span class="font_12 color_8b8b8b">恭喜{{userName}}用户秒杀到超级好礼</span>-->
      <!--</div>-->
      <div class="goods-list" style="overflow: auto;white-space: normal;height: 160px;">
        <van-row gutter="10">
          <van-col span="8" v-for="(item, index) in goodsListOne" @click.native="toGoodsKill(item)">
            <van-panel style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);border-radius: 3px;overflow: hidden">
              <div slot="header">
                <img :src="item.goodslogo.split(',')[0]" v-lazy="item.goodslogo.split(',')[0]" :loading="item.goodslogo.split(',')[0]" width="100%" style="display: block;" />
              </div>
              <div class="goods_content">
                <div class="goods_title" style="max-height: 48px;">
                  <span style="font-size: 14px">{{item.goodsname}}</span>
                  <br>
                  <span style="font-size: 12px;color:#8b8b8b;">{{item.introduce}}</span>
                </div>
              </div>
            </van-panel>
          </van-col>
        </van-row>
      </div>
      <div class="background_color_ffffff text_right" style="padding: 10px">
        <label style="float: left;">限时秒杀即将开始</label>
        <label class="font_12 color_8b8b8b">倒计时</label>
        <label class="count-down" v-if="!dhms">{{h}}:{{m}}:{{s}}</label>
        <label class="font_12 count-down" v-if="dhms">正在进行中</label>
      </div>
      <van-list v-model="loading" :finished="finished" @load="getmiaoshalist">
        <van-card v-for="(item, index) in goodsList" :key="index" style="margin: 10px;border-radius: 3px;" @click.native="toGoodsKill(item)">
          <div slot="thumb">
            <img :src="item.goodslogo.split(',')[0]" v-lazy="item.goodslogo.split(',')[0]" width="100%" style="display: block;"/>
          </div>
          <div slot="tags" class="font_weight_bold padding_bottom_5">
            {{item.goodsname}}
          </div>
          <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 28px;overflow: auto">
            {{item.introduce}}
          </div>
          <!--<div slot="tags" class="font_12 color_8b8b8b padding_bottom_5">-->
            <!--&nbsp;-->
          <!--</div>-->
          <div slot="tags" class="font_12 padding-top-5">
            <span class="color_ff3312">¥{{item.spike}}</span>
            <span class="padding-left-20">
              <img src="static/img/icon_ms_xsms.png" width="20" class="float_right"/>
            </span>
          </div>
        </van-card>
      </van-list>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'timeOfSecKill',
		data() {
			return {
        loading: false,
        finished: false,
        pageNum: 1,
        goodsList:[],
        userId:localStorage.getItem('userId'),
        goodsListOne: [],
        goodsList: [],
        d:'',
        h:'',
        m:'',
        s:'',
        dhms:false,
        userName:''
			}
		},
    mounted() {
      this.getmiaoshalist();
      this.getmiaoshalist1();
      this.countTime();
      this.getName();
    },
		methods:{
		  getName(){
        this.userName = Math.random().toString(36).substr(2);
        console.log('用户名为'+this.userName);
      },
      toGoodsKill(dataMap){
        this.$store.commit('setGoodsInfo', dataMap);
        this.$router.push('goodsKill');
      },
      countTime: function () {
        //获取当前时间
        var date = new Date();
        var now = date.getTime();
        //设置截止时间
        var endDate = new Date(new Date(new Date().toLocaleDateString()).getTime() + 8 * 60 * 60 * 1000 - 1);
        // var endDate = new Date("23:59:59");
        var end = endDate.getTime();
        //时间差
        var leftTime = end - now;
        //定义变量 d,h,m,s保存倒计时的时间
        if (leftTime >= 0) {
          this.d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
          this.h = Math.floor(leftTime / 1000 / 60 / 60 % 24);
          this.m = Math.floor(leftTime / 1000 / 60 % 60);
          this.s = Math.floor(leftTime / 1000 % 60);
        }else{
          this.dhms = true;
        }
        //递归每秒调用countTime方法，显示动态时间效果
        setTimeout(this.countTime, 1000);
      },
      getmiaoshalist(){
        this.loading = true;
        let $this = this;
        let obj = {
          userid: $this.userId,
          pageNum: $this.pageNum,
          cityid: localStorage.getItem('cityId')
        };
        let qs = require('qs');
        $this.$ajax.post("goods/getmiaoshalist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.goodsList = $this.goodsList.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
          }
        })
        .catch(function(err) {})
      },
      getmiaoshalist1(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          cityid: localStorage.getItem('cityId')
        };
        let qs = require('qs');
        $this.$ajax.post("goods/gettoplist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.goodsListOne = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .count-down{
    vertical-align: middle;
    color: #ff9c00;
    font-weight: bold;
  }
  .van-card {
    padding: 15px 5px 5px 115px;
  }
  .goods-list {
    padding: 5px;
  }
  .goods-list .goods_content {
    padding: 8px 4px;
  }
  .body_box{
    margin-top: 46px;
    overflow: auto;
  }
</style>



